import {Component, ElementRef, EventEmitter} from '@angular/core';
import {Http, Headers, RequestOptions, Response} from "@angular/http";
import {Router, RouterModule, ActivatedRoute} from '@angular/router';
import {SignUpService} from "./signup.service";
import { PlatformLocation } from '@angular/common'
import {Output} from "@angular/core/src/metadata/directives";

declare var jQuery: any;
declare var owlCarousel: any;


@Component({
  selector: 'signup',
  template: require('./signup.component.html'),
  styleUrls: ['./signup.component.css'],
  providers: [SignUpService]

})
export class SignUpComponent {
  private loginResult: any;
  @Output() signUpResult: EventEmitter<string> = new EventEmitter<string>();

  constructor(private signUpService: SignUpService ) {

  }


  ngOnInit(): void {
  }

  signup(userName: any, password: any){

    console.log(userName, password);

    this.signUpService.signUp(userName, password).subscribe(resultOut => {
      this.loginResult = resultOut

      if(this.loginResult == "true"){
        this.signUpResult.emit('TRUE');
      }

    });

  }



}



