import {Injectable, Input} from '@angular/core';
import 'rxjs/add/operator/map';
import {Http, Headers, RequestOptions, Response} from "@angular/http";
import {Observable} from "rxjs/Observable";
import {SignUpComponent} from "./signup.component";


@Injectable()
export class SignUpService {

  constructor(private _http: Http) {
  }

  // private urlHead = 'http://localhost/smrt-book/index1.php/';
  private urlHead = 'http://localhost/login_angular/signUp.php';

  signUp(userName: any, password: any) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/x-www-form-urlencoded; charset=UTF-8');
    let options = new RequestOptions({ headers: headers });

    return this._http.post(this.urlHead, JSON.stringify({userName: userName, password: password}), options)
      .map(this.extrctLog);
  }


  private extrctLog(res: Response) {

    let objArray = res.json();

    return objArray || {};
  }

}



